  left: 0;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  opacity: 0;
  background: #fff;
  visibility: hidden;
}

.preloader__visible {
  opacity: 1;
  visibility: visible;
}

.preloader-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.preloader-spinner-inner {
  -webkit-animation: rotate 2.5s linear infinite;
  animation: rotate 2.5s linear infinite;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.preloader-spinner-inner-path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke: #1c2545;
  -webkit-animation: dash 1.5s ease-in-out infinite;
  animation: dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
}

.preloader__visible .preloader-spinner {
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.header {
  font-size: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  box-shadow: 0 2px 6px 0px rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
}

.header .container {
  position: relative;
  padding: 0;
}

.header-inner {
  font-size: 0;
  padding: 20px 15px;
  background-color: #ffffff;
  position: relative;
  z-index: 2;
}

.header-inner:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #1c2545;
  opacity: 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.header-logo,
.header-navigation,
.header-search,
.header-toggle {
  display: inline-block;
