}

.header-logo {
  width: 160px;
}

.header-logo a {
  display: block;
  width: 160px;
  height: 44.5px;
  position: relative;
  top: 5px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 160px 44.5px;
  background-image: url(../images/logo.png);
}

.header-navigation {
  width: -o-calc(100% - 160px - 40px);
  width: -webkit-calc(100% - 160px - 40px);
  width: calc(100% - 160px - 40px);
}

.header-navigation__mobile {
  display: none;
  max-height: -o-calc(100vh - 56px);
  max-height: -webkit-calc(100vh - 56px);
  max-height: calc(100vh - 56px);
  overflow: auto;
}

.header-navigation__mobile li {
  display: block;
  width: 100%;
