.header-toggle {
  width: 35px;
  height: 28px;
  display: none;
  margin-left: 10px;
}

.header-search {
  width: 40px;
}

.header-search-toggle {
  width: 100%;
  padding-top: 100%;
  margin: 0;
  border: none;
  cursor: pointer;
  border-radius: 50%;
  position: relative;
  background-color: #1c2545;
}

.header-search-toggle:after,
.header-search-toggle:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70%;
  height: 70%;
  background-size: 70% 70%;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(../images/zoom.svg);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.header-search-toggle:before {
  background-image: url(../images/cancel.svg);
  opacity: 0;
}

.header-search-toggle:focus {
  outline: none;
}

.header-search-panel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  z-index: 1;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
