.header-search-panel * {
  height: 100%;
}

.header-search-panel label {
  height: 0;
}

.header-search-panel input {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #1c2545;
  padding: 0 10px;
  font-size: 14px;
}

.header-search-panel input:focus {
