.navigation {
  text-align: right;
  padding-left: 0;
  margin-bottom: 0;
}

.navigation:after {
  content: '';
  display: block;
  clear: both;
}

.navigation li {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  padding: 0 15px;
}

.navigation li a {
  font-size: 16px;
  color: #1d1d1b;
  text-transform: uppercase;
  position: relative;
  line-height: 1.2em;
  padding: 10px 0;
  display: block;
  text-decoration: none;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.navigation li a:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  width: 0;
  height: 1px;
  background-color: #1c2545;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.navigation li a:hover {
  text-decoration: none;
}

.navigation li a:hover:after {
  width: 100%;
  left: 0;
}

.navigation li.current-menu-item a:after {
  width: 100%;
  left: 0;
}

.navigation li .sub-menu {
  position: absolute;
  z-index: 1;
  top: 110%;
  left: 0;
  opacity: 0;
  visibility: hidden;
  background-color: #1c2545;
  padding: 0;
  margin: 0;
  width: 200px;
  text-align: center;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  margin-top: -1px;
  border: 1px solid #1c2545;
}

.navigation li .sub-menu li {
  display: block;
  width: 100%;
  padding: 0;
}

.navigation li .sub-menu li a {
  color: #ffffff;
}

.navigation li .sub-menu li a:after {
  display: none;
}

.navigation li .sub-menu li a:hover {
  background-color: #ffffff;
  color: #1d1d1b;
}

.navigation li .sub-menu .sub-menu {
  display: none;
}

.navigation li:hover .sub-menu {
  visibility: visible;
  opacity: 1;
  top: 100%;
}

.navigation__footer {
  text-align: left;
}

.navigation__footer li {
  float: left;
  display: block;
  padding: 0 10px 0 0;
