span.wpcf7-list-item {
  padding-right: 0;
  margin-left: 0;
}

.checkbox input[type=checkbox],
.checkbox-inline input[type=checkbox],
.radio input[type=radio],
.radio-inline input[type=radio] {
  margin-left: 0;
}

.checkbox {
  margin: 0;
  padding: 0 !important;
  padding-bottom: 15px !important;
}

.checkbox input {
  visibility: hidden;
  opacity: 0;
}

.checkbox-fake {
  position: relative;
  display: block;
  line-height: 1.2em;
  padding-left: 30px;
  color: #c6c6c5;
}

.checkbox-fake a {
  color: #c6c6c5;
  text-decoration: underline;
}

.checkbox-fake a:hover {
  text-decoration: none;
}

.checkbox-fake:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 20px;
  height: 20px;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  border: 1px solid #c6c6c5;
  border-radius: 3px;
  background-color: #fff;
  box-shadow: inset 0 0 0 10px #39425e;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.checkbox input:checked + .wpcf7-list-item-label .checkbox-fake:before {
  box-shadow: inset 0 0 0 5px #39425e;
}

.modal .checkbox-fake {
  color: #1c2545;
}

.modal .checkbox-fake a {
  color: #1c2545;
}




